@import '../../index.scss';

.errorSection {
    height: 80vh;
    color: $primary-color;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    &--404 {
        display: flex;
        flex-direction: column;
        align-items: center;
        h1 {
            font-weight: 700;
            font-size: 288px;
        }
        p {
            font-size: 36px;
            text-align: center;
        }
    }
    &--return {
        font-size: 18px;
        text-decoration: underline;
        cursor: pointer;
    }
}

@media screen and (max-width: 768px) {
    .errorSection {
        &--404 {
            h1 {
                font-size: 96px;
            }
            p {
                font-size: 18px;
            }
        }
        &--return {
            font-size: 14px;
        }
    }
}